// // Fetches document IDs based on product ID and document name
const getAttachedDocumentIds = async (
  baseUrl,
  productId,
  headers,
  parentRelName,
  docName
) => {
  try {
    const url = `${baseUrl}/resources/v1/modeler/documents/parentId/${productId}?parentRelName=${parentRelName}&tenant=OI000186152`;
    const response = await callEnoviaWebService("GET", url, "", headers);

    if (response?.status && response?.output?.data?.length) {
      console.log("docName", docName);
      console.log("docName", response.output.data);
      if (docName) {
        const filteredData = response.output.data.filter(item =>
          item?.dataelements?.name.toLowerCase().includes(docName.toLowerCase())
        );
        console.log("Filtered Data:", filteredData);
        return filteredData.map(item => item.id);
      } else {
        return response.output.data.map(item => item.id);
      }
    }

    return [];
  } catch (error) {
    console.error("Error fetching document IDs:", error);
    return [];
  }
};

// Helper function to build disconnect payload
const buildDisconnectPayload = (docIds, parentId, docType) => {
  const data = docIds.map((id) => ({
    id,
    updateAction: "NONE",
    relateddata: {
      parents: [{ id: parentId, updateAction: "DISCONNECT" }],
    },
  }));
  return { data, documentType: docType };
};

// Helper function to disconnect documents API
const disconnectDocuments = async (docs, headers, baseUrl, showErrorToast) => {
  console.log("Disconnecting documents:", docs);
  const results = [];
  for (const item of docs) {
    const url = `${baseUrl}/resources/v1/modeler/documents?parentRelName=${item.documentType}&parentDirection=from&tenant=OI000186152`;
    try {
      const res = await callEnoviaWebService("POST", url, item, headers);
      console.log(`Disconnected ${item.documentType}`, res);
    } catch (err) {
      console.error("Error disconnecting documents:", err);
      showErrorToast("Failed to disconnect documents for");
      results.push(item);
    }
    return results;
  }
};

// --------------------- Custom Hook -----------------------

const useRemoveDocConnection = () => {
  const [loading, setLoading] = useState(false);
  const { showErrorToast, showSuccessToast, showWarningToast } = useToast();
  const ENOVIA_BASE_URL = process.env.REACT_APP_ENOVIA_BASE_URL;

  const handleDocDisconnectingMassupload = async (sheetData) => {
    setLoading(true);
    const specDocs = [];
    const refDocs = [];
    const nonExistingProducts = [];
    const revisionNotMatch = [];
    const notInWorkProducts = [];
    const obsoleteProdcuts = [];
    let disconnectPayload = [];

    try {
      const headers = await fetchCsrfToken();
      if (!headers) return showErrorToast(MSG_FETCH_CSRF_HEADERS_FAILED);

      if (!sheetData?.length) {
        showErrorToast("No data to upload. Please check your sheet.");
        return;
      }
      console.log("Sheet data", sheetData);
      // ================= sheetData Loop =================
      for (const [index, row] of sheetData.entries()) {
        try {
          const searchStr = row["Item EIN"];
          const searchUrl = `${ENOVIA_BASE_URL}/resources/v1/modeler/dseng/dseng:EngItem/search?$searchStr=%5Bds6wg%3AEnterpriseExtension.V_PartNumber%5D%3A%2A${searchStr}%2A`;
          const response = await callEnoviaWebService(
            "GET",
            searchUrl,
            "",
            headers
          );

          const match = response?.output?.member?.find(
            (item) => item?.revision === String(row["Item Revision"])
          );

          if (!match) {
            nonExistingProducts.push(row);
            continue;
          }

          if (match.state !== "IN_WORK") {
            notInWorkProducts.push({ ...match, row });
            continue;
          }

          const docName = (row["Document Name"] || "").trim();
          let specIds = [];
          let refIds = [];

          if (match?.state === "OBSOLETE") {
            // Push the match object into an array for obsolete matches
            obsoleteProdcuts.push({ ...match, row });
            continue;
          } else {
            specIds = await getAttachedDocumentIds(
              ENOVIA_BASE_URL,
              match?.id,
              headers,
              "SpecificationDocument",
              docName
            );
            refIds = await getAttachedDocumentIds(
              ENOVIA_BASE_URL,
              match?.id,
              headers,
              "Reference%20Document",
              docName
            );
          }

          if (!specIds.length && !refIds.length) {
            showWarningToast(
              `No documents found for row ${index} with title ${row["Item EIN"]}`
            );
            continue;
          }

          if (specIds.length) {
            // If there are Specification documents, build the payload helper function
            const payload = buildDisconnectPayload(
              specIds,
              match?.id,
              "SpecificationDocument"
            );
            specDocs.push(payload);
            // Call disconnectDocuments for SpecificationDocument API
            const disconnectResults = await disconnectDocuments(
              [payload],
              headers,
              ENOVIA_BASE_URL,
              showErrorToast
            );
            disconnectPayload.push(...disconnectResults);
          }

          if (refIds.length) {
            // If there are reference documents, build the payload helper function
            const payload = buildDisconnectPayload(
              refIds,
              match?.id,
              "Reference%20Document"
            );
            refDocs.push(payload);
            // Call disconnectDocuments for reference documents API
            const disconnectResults = await disconnectDocuments(
              [payload],
              headers,
              ENOVIA_BASE_URL,
              showErrorToast
            );
            disconnectPayload.push(...disconnectResults);
          }
        } catch (rowError) {
          console.error(`Error processing row ${index}:`, rowError);
        }
      }

      // ================= End of sheetData Loop =================
      console.log("Specification Docs:", specDocs);
      console.log("Reference Docs:", refDocs);
      console.log("revisionNotMatch", revisionNotMatch);
      console.log("notInWorkProducts", notInWorkProducts);
      console.log("obsoleteProdcuts", obsoleteProdcuts);
      console.log("disconnectPayload ", disconnectPayload);
      showSuccessToast("Disconnected Documents completed successfully.");
    } catch (mainError) {
      console.error("Mass upload error:", mainError);
      showErrorToast("An error occurred while processing the mass upload.");
    } finally {
      setLoading(false);
    }
  };
  return {
    handleDocDisconnectingMassupload,
    loading,
  };
};

export default useRemoveDocConnection;
